.admin-container {
    width: 90%;
    margin: 20px auto;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.admin-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 10px;
    border-bottom: 2px solid #ffc107;
}

.admin-header h1 {
    margin: 0;
    font-size: 24px;
    color: #333;
}

.admin-tabs {
    display: flex;
    margin-bottom: 20px;
    border-bottom: 2px solid #ffc107;
}

.admin-tab {
    padding: 10px 20px;
    cursor: pointer;
    font-weight: bold;
    color: #333;
    border: 1px solid #ddd;
    border-bottom: none;
    background-color: #fff;
}

.admin-tab.active {
    background-color: #ffc107;
    color: #fff;
    border-bottom: 2px solid #ffc107;
}

.table-controls {
    margin-bottom: 20px;
    display: flex;
    gap: 10px;
}

.table-controls input {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
    font-size: 14px;
}

.table-controls .control-button {
    padding: 5px 10px;
    background-color: #ffc107;
    border: none;
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    font-weight: bold;
}

.table-controls .control-button:hover {
    background-color: #e0a800;
}

.member-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.member-table th,
.member-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
    font-size: 14px;
}

.member-table th {
    background-color: #f8f9fa;
    font-weight: bold;
    color: #333;
}

.member-table tr:hover {
    background-color: #f1f1f1;
}

.pagination {
    display: flex;
    justify-content: center;
    gap: 5px;
}

.pagination button {
    padding: 5px 10px;
    border: 1px solid #ddd;
    background-color: #fff;
    color: #333;
    cursor: pointer;
    border-radius: 3px;
}

.pagination button.active {
    background-color: #ffc107;
    color: #fff;
    font-weight: bold;
}

.pagination button:hover {
    background-color: #e0a800;
    color: #fff;
}

.pagination button:disabled {
    background-color: #f8f9fa;
    color: #aaa;
    cursor: not-allowed;
}
